.tab-container {
  margin-bottom: 30px;
  margin-top: 41px;
}

.tab-container .tab {
  width: 50%;
  border: none;
  padding: 18px;
  font-weight: bold;
  color: #ee6e73;
  background-color: white;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.4);
  outline: none;
  transition-duration: .3s;
}

.tab-container .tab:hover {
  color: white;
  background-color: #ee9ea6;
  cursor: pointer;
}

.tab-container .active {
  color: white;
  background-color: #ee6e73;
}

.project {
  display: flex;
  background-color: #fff;
  border-radius: 8px;
  padding: 10px 6px;
  box-shadow: 1px 1px 6px grey;
  transition-duration: .3s;
  max-width: 720px;
  margin: 26px auto;
}

.project:hover {
  box-shadow: 1px 1px 12px rgba(0, 0, 0, 0.6);
}

.project-image-area {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.project-img {
  display: block;
  width: auto !important;
  max-width: 100% !important;
  max-height: 200px;
  contain: content;
  cursor: pointer;
}

.project-description-area {
  flex: 2;
  padding: 6px 10px;
}

.project-name {
  font-size: 24px;
  font-weight: 700;
  color: #222;
  text-align: center;
  display: block;
}

.project-description {
  text-align: left;
  display: block;
}

hr {
  width: 100%;
	border: none;
	height: 2px;
	background-color: #dedede;
}

.built-with span {
  padding: 4px 6px;
  margin-right: 8px;
  margin-bottom: 6px;
  display: inline-block;
  box-shadow: 0 0 1px grey;
  border-radius: 2px;
  font-weight: 600;
}

.project-link-wrapper {
  display: flex;
  justify-content: flex-end;
}

.project-link {
  position: relative;
  display: block;
  padding: 4px 8px;
  margin-bottom: -4px;
  align-content: stretch;
  font-weight: 600;
  text-align: right;
  text-decoration: none;
  color: orangered;
  opacity: 0.9;
}

.project-link:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left:0;
  right: 0;
  background: linear-gradient(90deg, orangeRed 50%, #fff 50%);
  z-index: -1;
}

.project-link, .project-link:after {
  background-size: 200%;
  background-position: 100%;
  transition: 0.6s cubic-bezier(0.19, 1, 0.22, 1);
}

.project-link:hover, .project-link:hover:after {
  background-position: 0;
  color: white;
}

.see-more {
  text-decoration: none;
  color: #222;
  display: block;
  font-weight: 600;
  text-align: right;
  margin-right: 16px;
  margin-bottom: 16px;
}