/*-----------ABOUT------------>*/

#profile {
  margin-top: 40px;
}

#profile h1{
  text-align: center;
  color: green;
  font-weight: 600;
}

#profile span{
  display: block;
  font-size: 16px;
  text-align: center;
  color: black;
  margin: 0 10vw
}

#profile img {
  display: block;
  margin: 0 auto;
  width: 200px;
  height: auto;
  border-radius: 50%;
  border: none;
  pointer-events: none;
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.6);
}
