/*-----------CONTACT------------>*/

#contact {
  background-color: wheat;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#contact a {
  text-decoration: none;
  margin:10px auto;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  color: black;
  font-weight: bold;
}

#contact a img {
  width:40px;
  height: auto;
  margin: 0 8px;
}

#contact a span {
  display: block;
  margin-top: 8px
}

.social {
  margin:10px auto
}
